.link-header {
  margin-right: 30px;
  display: flex;
  margin-top: 45px;

  @include focus {
    outline: 1px solid $color-deep-sky-blue;
  }

  &:last-child {
    margin-right: 0;
  }
}

.link-header__text {
  font-family: $ff-mono;
  font-size: 16px;
  color: $color-alice-blue;
  margin-left: 15px;
  display: block;
  margin-top: -3px;
}

.link-header__icon {
  margin: 0;
}

@media (max-width: $tablet-width-max) {
  .link-header {
    margin-top: 20px;
    margin-right: 20px;
  }

  .link-header__icon {
    margin: 10px;

    @include hover {
      opacity: 0.6;
    }
  }

  .link-header__text {
    display: none;

    @include hover {
      opacity: 0.6;
    }
  }
}

@media (max-width: $mobile-width-max) {
  .link-header {
    margin-top: 10px;
  }
}
